import { useState, useEffect } from "react"
import Axios from "axios"
import UserInfo from "./userInfo"
import "./home.css"
import "./btn.css"

function Home() {
    const [currentPage, setCurrentPage] = useState("home")
    const [userList, setUserList] = useState([])
    const [name, setName] = useState("")
    const [userId, setUserId] = useState("")
    const [age, setAge] = useState(0)
    const [taxIncome, setTaxIncome] = useState(0)
    const [taxIncome60, setTaxIncome60] = useState(0)
    const [rate, setRate] = useState(1)
    const [maxAge1, setMaxAge1] = useState(0)
    const [maxAge2, setMaxAge2] = useState(0)
    const [afterIncome, setAfterIncome] = useState(0)
    const [pension, setPension] = useState(0)
    const [hasWife, setHasWife] = useState(0)
    const [saving, setSaving] = useState(0)

    /*アプリ利用者一覧を取得 */
    useEffect(() => {
        Axios.get("https://modest-itoman-5891.lolipop.io/getUserInfo").then((response) => {
            setUserList(response.data)
            console.log(response.data)
        })
    }, [])

    /*利用者を選択したときの処理 */
    const selectUser = (name, userId, age, taxIncome, taxIncome60, rate, maxAge1, maxAge2, afterIncome, pension, hasWife, saving) => {
        setName(name)
        setUserId(userId)
        setAge(age)
        setTaxIncome(taxIncome)
        setTaxIncome60(taxIncome60)
        setRate(rate)
        setMaxAge1(maxAge1)
        setMaxAge2(maxAge2)
        setAfterIncome(afterIncome)
        setPension(pension)
        setHasWife(hasWife)
        setSaving(saving)
        setCurrentPage("userInfo")
    }

    /*ホームに戻る */
    const backHome = () => {
        setName("")
        setUserId("")
        setAge(0)
        setTaxIncome(0)
        setTaxIncome60(0)
        setPension(0)
        setHasWife(0)
        setSaving(0)
        setCurrentPage("home")
    }
    let page;
    if (currentPage === "home") {
        page = <div className="home-wrapper">
            <h2 className="home-title">アプリ利用者一覧</h2>
            <table className="home-table">
                <tr className="table-header">
                    <th>お名前</th>
                    <th>年収</th>
                    <th>配偶者の有無</th>
                    <th>ご利用日付</th>
                </tr>
                {userList.map((data, index) => {
                    return (
                        <tr className="table-row" key={index} onClick={() => {
                            selectUser(data.name, data.userId, data.age, data.taxIncome, data.taxIncome60, data.rate, data.maxAge1, data.maxAge2, data.afterIncome, data.pension, data.hasWife, data.saving)
                        }}>
                            <td className="table-data">{data.name} 様</td>
                            <td className="table-data">{data.taxIncome}</td>
                            {data.hasWife === 1 ?
                                <td className="table-data">あり</td>
                                :
                                <td className="table-data">なし</td>
                            }
                            <td className="table-data">{data.temp_date}</td>
                        </tr>
                    )
                })}
            </table>
        </div>
    } else if (currentPage === "userInfo") {
        page = <UserInfo name={name} userId={userId} age={age} taxIncome={taxIncome}
            taxIncome60={taxIncome60} pension={pension} rate={rate} maxAge1={maxAge1}
            maxAge2={maxAge2} afterIncome={afterIncome} hasWife={hasWife} saving={saving} backHome={backHome} />
    }
    return (
        <>
            {page}
        </>
    )
}
export default Home