import './App.css';
import { useState, useEffect } from "react"
import Axios from "axios"
import Home from './component/home';
import "./component/btn.css"
import "./component/input.css"

function App() {
  const [isLoggedIn, setIsLoggedIn] = useState(false)
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")
  const [emailError, setEmailError] = useState(false)
  const [passError, setPassError] = useState(false)

  useEffect(() => {
    const strage = localStorage.getItem("loggedData")
    const loggedData = JSON.parse(strage)
    if (loggedData !== null) {
      setIsLoggedIn(true)
    }
  }, [])

  const login = () => {
    Axios.post("https://modest-itoman-5891.lolipop.io/login", {
      email: email,
      password: password
    }).then((response) => {
      console.log(response.data)
      if (response.data.isLoggedIn) {
        setIsLoggedIn(true)
        var array = []
        var obj = {
          "isLoggedIn": true,
        }
        array.push(obj)
        var setjson = JSON.stringify(obj)
        window.localStorage.setItem("loggedData", setjson)
      } else {
        if (response.data.errorMsg === "email") {
          setEmailError(true)
        } else {
          setPassError(true)
        }
      }
    })
  }

  const logout = () => {
    setIsLoggedIn(false)
    localStorage.removeItem("loggedData")
  }

  return (
    <div className="App">

      {isLoggedIn ?
        <>
          <div className="header">
            <h1 className="title">管理者ページ</h1>
            <button className="log-out" onClick={logout}><i class="fas fa-sign-out-alt"> ログアウト</i></button>
          </div>
          <Home />
        </>
        :
        <>
          <div className="header">
            <h1 className="title">管理者ページ</h1>
          </div>
          {emailError ?
            <p>メールアドレスが間違っています</p>
            : null}
          {passError ?
            <p>パスワードが間違っています</p>
            : null}

          <div className="input-wrapper">
            <div class="cp_iptxt">
              <input class="ef" type="email" placeholder="" value={email} onChange={(e) => { setEmail(e.target.value) }} />
              <label>メール</label>
              <span class="focus_line"></span>
            </div>

            <div className="cp_iptxt">
              <input className="ef" type="password" placeholder="" value={password} onChange={(e) => { setPassword(e.target.value) }} />
              <label>パスワード</label>
              <span className="focus_line"></span>
            </div>
          </div>

          <button onClick={login}>ログイン<i class="fas fa-sign-in-alt"></i></button>
        </>

      }
    </div>
  );
}

export default App;
