import { useState, useEffect, memo } from "react"
import Axios from "axios"
import Transition from "./transition"
const UserInfo = memo((props) => {
    const { name, userId, age, taxIncome, taxIncome60, rate, maxAge1,maxAge2,afterIncome, pension, hasWife, saving, backHome } = props
    const [currentPage, setCurrentPage] = useState("info")
    const [info] = useState({ name: name, age: age, taxIncome: taxIncome, taxIncome60: taxIncome60, rate: rate, maxAge1: maxAge1,maxAge2:maxAge2,afterIncome:afterIncome, pension: pension })
    const [wifeInfo, setWifeInfo] = useState({ age: 0, taxIncome: 0, taxIncome60: 0, rate: 1, maxAge1: 0,maxAge2:0,afterIncome:0, pension: 0 })
    const [huyoList, setHuyoList] = useState([])
    const [payList, setPayList] = useState([])
    const [incomeList, setIncomeList] = useState([])

    /*ユーザーIDから配偶者、扶養、支出、その他収入情報を所得 */
    useEffect(() => {
        console.log(userId)
        console.log(hasWife)
        if (hasWife === 1) {
            Axios.get(`https://modest-itoman-5891.lolipop.io/getWifeInfo/${userId}`).then((response) => {
                
                console.log(response.data)
            })
        }
        Axios.get(`https://modest-itoman-5891.lolipop.io/getHuyoList/${userId}`).then((response) => {
            setHuyoList(response.data)
            console.log(response.data)
        })

        Axios.get(`https://modest-itoman-5891.lolipop.io/getPayList/${userId}`).then((response) => {
            setPayList(response.data)
            console.log(response.data)
        })

        Axios.get(`https://modest-itoman-5891.lolipop.io/getIncomeList/${userId}`).then((response) => {
            setIncomeList(response.data)
            console.log(response.data)
        })
    }, [hasWife, userId])

    const openTransition = () => {
        setCurrentPage("transition")
    }

    const openInfo = () => {
        setCurrentPage("info")
    }

    let page;
    if (currentPage === "info") {
        page = <div className="page-wrapper info-contents">
            <h3>本人情報</h3>
            <p>お名前:{name}</p>
            <p>年齢:{age}歳</p>
            <p>年収:{taxIncome}万円</p>
            <p>60~65歳の年収:{taxIncome60}万円</p>
            <p>昇給率:{rate}</p>
            <p>最大昇給年齢:{maxAge1}</p>
            <p>最大年収年齢:{maxAge2}</p>
            <p>最大年収年齢後の年収:{afterIncome}</p>
            <p>年金:{pension}万円</p>
            <p>貯金残高:{saving}万円</p>
            <h3>配偶者情報</h3>
            {hasWife === 1 ?
                <>
                    <p>年齢:{wifeInfo.age}歳</p>
                    <p>年収:{wifeInfo.taxIncome}万円</p>
                    <p>60~65歳の年収:{wifeInfo.taxIncome60}万円</p>
                    <p>昇給率:{wifeInfo.rate}</p>
                    <p>最大昇給年齢:{wifeInfo.maxAge1}</p>
                    <p>最大年収年齢:{wifeInfo.maxAge2}</p>
                    <p>最大年収年齢後の年収:{wifeInfo.afterIncome}</p>
                    <p>年金:{wifeInfo.pension}万円</p>
                </>
                :
                <p>配偶者：なし</p>}
            <h3>扶養情報</h3>
            {huyoList.map((data, index) => {
                if (data.type === "child") {
                    return (
                        <div key={index}>
                            <label>種類:~22歳扶養/年齢{data.childAge}歳/控除対象者:
                                {data.self === 0 ?
                                    <span>本人</span>
                                    :
                                    <span>配偶者</span>}
                            </label>
                            <br />
                        </div>

                    )
                } else if (data.type === "wife") {
                    return (
                        <div key={index}>
                            <label>種類:配偶者/人数:1/控除対象者:
                                {data.self === 0 ?
                                    <span>本人</span>
                                    :
                                    <span>配偶者</span>}
                            </label>
                            <br />
                        </div>
                    )
                } else if (data.type === "other") {
                    return (
                        <div key={index}>
                            <label>種類:その他扶養/人数:{data.numberOfHuyo}/控除対象者:
                                {data.self === 0 ?
                                    <span>本人</span>
                                    :
                                    <span>配偶者</span>}
                            </label>
                            <br />
                        </div>
                    )
                } else if (data.type === "senior1") {
                    return (
                        <div key={index}>
                            <label>種類:老人同居/人数:{data.numberOfHuyo}/控除対象者:
                                {data.self === 0 ?
                                    <span>本人</span>
                                    :
                                    <span>配偶者</span>}
                            </label>
                            <br />
                        </div>
                    )
                } else {
                    return (
                        <div key={index}>
                            <label key={index}>種類:老人別居/人数:{data.numberOfHuyo}/控除対象者:
                                {data.self === 0 ?
                                    <span>本人</span>
                                    :
                                    <span>配偶者</span>}
                            </label>
                            <br />
                        </div>
                    )
                }

            })}
            <h3>支出情報</h3>
            {payList.map((data, index) => {
                let typeLabel1;
                if (data.type === "house") {
                    typeLabel1 = "住宅ローン、家賃"
                } else if (data.type === "car") {
                    typeLabel1 = "車（ローン）"
                } else if (data.type === "nomal") {
                    typeLabel1 = "一般支出"
                } else if (data.type === "sisanTax") {
                    typeLabel1 = "固定資産税"
                } else if (data.type === "seimeiHoken") {
                    typeLabel1 = "生命保険"
                } else if (data.type === "study") {
                    typeLabel1 = "学費など"
                } else if (data.type === "other") {
                    typeLabel1 = "その他の支出"
                } else if (data.type === "living") {
                    typeLabel1 = "65歳までの月々の生活費"
                } else {
                    typeLabel1 = "65歳以降の月々の生活費"
                }
                let typeLabel2;
                if (data.type2 === "every") {
                    typeLabel2 = "毎年"
                } else if (data.type2 === "before") {
                    typeLabel2 = "まで毎年"
                } else if (data.type2 === "after") {
                    typeLabel2 = "から毎年"
                } else {
                    typeLabel2 = "の時に1回限り"
                }
                return (
                    <div key={index}>
                        <label>種類:{typeLabel1}/金額（年額）:{data.charge}万円/回数:{typeLabel2}</label>
                        <br />
                    </div>
                )

            })}

            <h3>その他収入情報</h3>
            {incomeList.map((data, index) => {
                let typeLabel1;
                if (data.type === "retire") {
                    typeLabel1 = "退職金（万円）"
                } else if (data.type === "other") {
                    typeLabel1 = "その他収入（万円）"
                } else if (data.type === "yachin") {
                    typeLabel1 = "家賃収入（万円）"
                } else if (data.type === "nenkin") {
                    typeLabel1 = "個人年金（万円）"
                } else if (data.type === "nenkinW") {
                    typeLabel1 = "個人年金配偶者（万円）"
                } else {
                    typeLabel1 = "その他（万円）"
                }
            
                let typeLabel2;
                if (data.type2 === "every") {
                    typeLabel2 = "毎年"
                } else if (data.type2 === "before") {
                    typeLabel2 = "まで毎年"
                } else if (data.type2 === "after") {
                    typeLabel2 = "から毎年"
                } else {
                    typeLabel2 = "の時に1回限り"
                }
                return (
                    <div key={index}>
                        <label>種類:{typeLabel1}/金額（年額）:{data.charge}万円/回数:{typeLabel2}</label>
                        <br />
                    </div>
                )
            })}
            <br />

        </div>
    } else {
        page = <Transition name={name} info={info} wifeInfo={wifeInfo} saving={saving} huyoList={huyoList}
            payList={payList} incomeList={incomeList} />
    }
    return (
        <div className="info-wrapper">
            <h1 className="info-title">{name}さんの情報</h1>
            {page}
            <div className="button-wrapper">
                {currentPage === "info"
                    ?
                    <>
                        <div onClick={backHome}><i class="fas fa-caret-square-left"></i></div>
                        <button onClick={openTransition}>推移ページを見る</button>
                    </>
                    :
                    <>
                        <div onClick={openInfo}><i class="fas fa-caret-square-left"></i></div>
                        <div onClick={backHome}><i class="fas fa-home"></i></div>
                    </>
                }
            </div>
        </div>

    )
})
export default UserInfo